*{
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
    padding: 0;
    /* overflow: hidden; */
}

.background {
    height: 100%;
    background-color: #000;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 150px 1fr;
    border-radius: 1rem;
    margin: 0 1rem;
    grid-template-areas: "aside content content"
                        "aside content content"
}


:root {
    --primary-color: rgba(240, 236, 236, 0.8);
    --header-color: rgba(240, 236, 236, 0.4);
    --secondary-color: greenyellow;
}

.option {
    color: rgba(240, 236, 236, 0.8);
    color: var(--primary-color);
    /* border-right: 1px solid var(--primary-color); */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    grid-area: aside;
    margin-top: 2.5rem;
    height: 100%;
}

.option__title , .option__suggestion_title{
    font-size: 12px;
    color: rgba(240, 236, 236, 0.4);
    color: var(--header-color);
    padding: 0 1rem;
    margin: 2rem 0 0 0;
    font-weight: 200;
}

.option__list {
    list-style-type: none;
    font-size: 12px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0.6rem 0;
}

.option__list li {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding-left: 1.5rem;
    margin-top: 0.1rem;
    padding-bottom: 0.3rem;
}

.option__list li:last-child {
    padding-bottom: 0;
}

.option__list li:hover,
.option__list li:active  {
    border-left: 2px solid greenyellow;
    border-left: 2px solid var(--secondary-color);
    margin-right: 1rem;
    margin-left: 0;
    cursor: pointer;
}

.option__suggestion {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}


.option__suggestion_title {
    margin-top: 1rem;
}

.option__suggestion div {
    font-size: 12px;
    color: greenyellow;
    color: var(--secondary-color);
    margin-left: 1.5rem;
    cursor: pointer;
}

.option__suggestion div:hover,
.option__suggestion div:active{
    color: #fff;
    margin-left: 2rem;
}


/*================Media Query=================*/

@media screen and (max-width: 768px) {

    .option {
        margin-top: 1rem;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
    }
}
.song-lyric {
    color: #fff;
    position: absolute;
    top: 15px;
    text-align: center;
    left: -15rem;
    background: hsla(0, 0%, 0%, 0.3);
    padding: 1rem 4rem;
    margin-left: 12rem;
}

/*==============Media Queries============*/
@media screen and (max-width: 768px) {
    .song-lyric {
        /* left: -6rem; */
        /* padding-left: 6rem; */
    }

}


